const artical = [
  { title: "Why", content: "We think technology is over-complicating our lives. Our goal is to make life simpler through our services and products.", link: '/philosophy' },
  { title: "What", content: "We offer software development services, and we make our own products, both of which focus on helping you get what you want without hassle.", link: '/what_we_do' },
  { title: "How", content: "We produce excellent work and take care of our clients while maintaining work/life balance, empathy, and compassion for our communities.", link: '/how_we_work' },
]

const listWordHome = ['ENJOYED', 'LIVED', 'SIMPLIFIED', 'BALANCED', 'MODERNIZED', 'UNCLOUDED'];

const BlogIndex = ({ data, location }) => {
  const classes = useStyles()
  const siteTitle = data.site.siteMetadata?.title || `Title`;
  return (
    <Layout location={location} title={siteTitle}>
      <Seo title="Welcome to Izundo" img="src/images/gatsby-icon.png"/>
      <div>
        <div className={classes.image}>
          <div>
            <p className={classes.dynamicContent}>
              <span className="hideEvery2500sec">LIFE:</span> <FlickerEffect listWord={listWordHome}></FlickerEffect>
            </p>
          </div>
        </div>
        <div className={classes.listPerson}>
          {artical.map((atc, i) => {
            return (
              <article key={i}>
                <p>{atc.title}</p>
                <p className={classes.configArtical}>{atc.content}</p>
                <p><Link to={atc.link} alt={"izundo"+atc.link}>MORE INFO</Link></p>
              </article>)
          })}
        </div>
      </div>
    </Layout>
  )
}

export default BlogIndex

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      nodes {
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
          description
        }
      }
    }
  }
`